<template>
    <div class="card">
        <font-awesome-icon @click="manageFavourite(id)" :class="checkFavourite(id)? 'icon-favourite' : 'icon-unfavourite'" icon="fa-solid fa-heart" />
        <div class="card-img-top" v-for="images in imageUrls" :key="images">
            <img :src="images.front_default">
        </div>
        <div class="card-body">

            <h5 class="card-title">#{{ id }} | {{ name.toUpperCase() }}</h5>
            <table class="stats-table">
                <tr v-for="stat in stats" :key="stat">
                    <td class="stat-name">{{ stat.stat.name.toUpperCase() }}</td>
                    <td class="stat-value">{{ stat.base_stat }}</td>
                </tr>
            </table>
        </div>
        <base-button type="button" mode="details" @click="loadPokemonDetails">Details</base-button>

        


    </div>
</template>

<script>

export default {
    props: ['id', 'imageUrls', 'stats', 'name'],
    data() {
        return {
            isFavourite: false,
        }
    },
    methods: {
        loadPokemonDetails() {
            this.$router.push({
                name: 'pokemonDetails',
                params: { id: this.id }
            });
        },
        manageFavourite(id) {
            const storedPokemon = JSON.parse(localStorage.getItem('favouritePokemon'));
            console.log(this.checkFavourite(id), id)

            if (!this.checkFavourite(id)) {
                let favPokemon = []
                if (storedPokemon !== null) {
                    favPokemon = storedPokemon;
                }
                favPokemon.push({
                    pokeId: id
                })
                localStorage.setItem("favouritePokemon", JSON.stringify(favPokemon));

                if(this.$route.path === '/pokemons'){
                    this.$router.go('/favourite');
                }
                this.$router.go();

    
            } else {
                let updatedPokemon = [];
                if (storedPokemon !== null) {
                    updatedPokemon = storedPokemon.filter(array => array.pokeId !== id);
                }
                if (updatedPokemon.length > 0) {
                    localStorage.setItem("favouritePokemon", JSON.stringify(updatedPokemon));
                } else {
                    localStorage.removeItem('favouritePokemon');

                }
                if(this.$route.path === '/favourite'){
                    this.$router.go('/pokemons');
                }
                this.$router.go();

            }

        },

        checkFavourite(id) {
            const favPokemon = JSON.parse(localStorage.getItem('favouritePokemon'));
            if (favPokemon === null) {
                return;
            }
            return favPokemon.some(array => array.pokeId === id);
        }
    }
}
</script>

<style scoped>
img {
    width: 80%;
    max-width: 200px;
    height: auto;
}

.card {
    flex: 1 0 calc(25% - 10px) !important;
    margin: 5px;
    min-width: 10rem;
    max-width: 15rem;
    justify-content: center;
    align-items: center;
}

.card-img-top {
    text-align: center;
}

.stat-name,
.stat-value {
    text-align: center;
    font-size: 12px;
    border: 1px solid grey;
}

.stat-name {
    background-color: lightgrey;
}

.stat-value {
    padding: 2px 12px;
}
.card-title{
    font-size: 15px;
    text-align:center;
}

.icon-favourite{
    color:crimson;
    cursor:pointer;
    font-size: 26px;
}

.icon-unfavourite{
    color:rgb(36, 36, 36);
    cursor:pointer;
    font-size: 26px;


}
</style>