<template>
  <div class="filter">
    <div class="form-group">
      <label for="searchText">Search Text:</label>
      <input
        id="searchText"
        v-model="searchText"
        name="searchText"
        v-on:keyup.enter="searchPokemon"
        class="form-control"
        placeholder="Enter search text here"
      />
    </div>
    <div class="form-group">
      <label for="criteria">Search By:</label>
      <select class="form-control" id="criteria" name="criteria" v-model="criteria">
        <option value="name">Pokemon Name</option>
        <option value="id">Pokemon ID</option>
      </select>
    </div>
    <div class="form-group">
      <label for="dataPerPage">Data:</label>
      <select class="form-control" id="dataPerPage" name="dataPerPage" v-model="dataPerPage" @change="setDataPerPage">
        <option value=15>15</option>
        <option value=30>30</option>
        <option value=50>50</option>
      </select>
    </div>
  </div>
</template>

<script>
export default {
    emits:['search-pokemon','data-per-page'],
  data() {
    return {
      searchText: "",
      criteria: "name",
      dataPerPage: 15
    };
  },
  methods:{
    searchPokemon(){
        const searchCriteria ={
            searchText :this.searchText,
            criteria: this.criteria
        }
        this.$emit('search-pokemon', searchCriteria);
    },
    setDataPerPage(){
      this.$emit('data-per-page', this.dataPerPage)
    }
  },
  
};
</script>

<style scoped>
  .filter{
    display: flex ;
    flex-direction: row;
    align-content: right;
    justify-content: center;
  }
  .form-group{
    margin:3px;
  }
</style>