<template> 
<div class="sort">
<div class="form-group">
    <label for="stats">Sort By:</label>
     <select class="form-control" id="stats" name="stats" v-model="stats" @change="setSequence">
        <option value="hp">HP</option>
        <option value="att">ATT</option>
        <option value="def">DEFENSE</option>
        <option value="id">Pokemon ID</option>
      </select>
</div>
<div class="form-group">
    <label for="sequence">Order:</label>
    <select class="form-control" id="sequence" name="sequence" v-model="sequence" @change="setSequence">
        <option value="asc">Ascending</option>
        <option value="des">Descending</option>
      </select>
</div>
</div>
   
      
</template>

<script> 
export default {
    emits:['set-sequence'],
    data(){
        return{
            stats: "id",
            sequence: "asc"
        }
    },
    methods:{
        setSequence(){
            let sequence = {
                stats : this.stats,
                sequence : this.sequence
            }
            this.$emit('set-sequence', sequence);
        }
    }
}
</script>

<style scoped>
.sort{
    display:flex;
    flex-direction:row;
    justify-content: center;
    align-items: center;
}

.form-group{
    margin:3px;
}
</style>