<template>
  <div class="container" v-if="!isLoading">
  
    <div v-if="!hasDetails">No details</div>
    <div v-else>
      <div class="details-header">
        <base-button class="next" mode="flat" @click="nextPageLink">Next</base-button>
        <h2 class="pokemon-name">
          #{{ pokemonDetails.responseData.id }}
          {{ pokemonDetails.responseData.name.toUpperCase() }}
        </h2>
        <base-button class="previous" mode="flat" @click="previousPageLink"
        >Previous</base-button
      >
      </div>
      <div class="general-info">
        <div class="pokemon-image">
          <img
            :src="pokemonDetails.responseData.sprites.other.home.front_default"
            :alt="pokemonDetails.responseData.name"
          />
        </div>
        <div class="pokemon-info">
          <div class="pokemon-biodata">
            <p class="biodata-title">Height</p>
            <p class="biodata-value">
              {{ pokemonDetails.responseData.height * 10 }} kg
            </p>

            <p class="biodata-title">Weight</p>
            <p class="biodata-value">
              {{ pokemonDetails.responseData.weight * 0.1 }} cm
            </p>

            <p class="biodata-title">Gender</p>
            <p class="biodata-value">{{ pokemonGenderPercentage }}</p>
          </div>
          <div class="pokemon-ability">
            <p class="biodata-title"> Abilities</p>
            <div
              class="abilities"
              v-for="(ability, index) in pokemonDetails.abilityData"
              :key="ability"
            >
              <button
                type="button"
                class="ability-button"
                data-toggle="tooltip"
                data-placement="top"
                :title="toolText(index)"
              >
                {{ ability.name }}
              </button>
            </div>
          </div>
          <p class="biodata-title">Type</p>
          <div
            class="pokemon-types"
            v-for="pokemonType in pokemonDetails.responseData.types"
            :key="pokemonType"
          >
            <base-type-tag :types="pokemonType.type.name"
              >{{ pokemonType.type.name.toUpperCase() }}
            </base-type-tag>
          </div>
        </div>
      </div>
        <h3 class="details-title">Damage Relationship</h3>

      <div
        class="pokemon-weaknesses"
        v-for="typeData in pokemonDetails.typeData"
        :key="typeData"
      >
      <div class="pokemon-type">
        <p>Type:  <base-type-tag :types="typeData.name"
              >{{ typeData.name.toUpperCase() }}
            </base-type-tag></p>

        
      </div>
        <div class="damage-info">
          <div class="double-damage">
            <p class="dmg-title">Double Damage From</p>
            <div
              class="pokemon-types"
              v-for="types in typeData.damage_relations.double_damage_from"
              :key="types"
            >
              <base-type-tag :types="types.name">
                {{ types.name.toUpperCase() }}
              </base-type-tag>
            </div>
          </div>
          <div class="double-damage">
            <p class="dmg-title">Double Damage To</p>
            <div
              class="pokemon-types"
              v-for="types in typeData.damage_relations.double_damage_to"
              :key="types"
            >
              <base-type-tag :types="types.name">
                {{ types.name.toUpperCase() }}
              </base-type-tag>
            </div>
          </div>
        </div>
        <div class="damage-info">
          <div class="half-damage">
            <p class="dmg-title">Half Damage From</p>
            <div
              class="pokemon-types"
              v-for="types in types in
              typeData.damage_relations.half_damage_from"
              :key="types"
            >
              <base-type-tag :types="types.name">
                {{ types.name.toUpperCase() }}
              </base-type-tag>
            </div>
          </div>
          <div class="half-damage">
            <p class="dmg-title">Half Damage To</p>
            <div
              class="pokemon-types"
              v-for="types in typeData.damage_relations.half_damage_to"
              :key="types"
            >
              <base-type-tag :types="types.name">
                {{ types.name.toUpperCase() }}
              </base-type-tag>
            </div>
          </div>
        </div>
        <div class="damage-info">
          <div class="no-damage">
            <p class="dmg-title">No Damage From</p>
            <div
              class="pokemon-types"
              v-for="types in types in typeData.damage_relations.no_damage_from"
              :key="types"
            >
              <base-type-tag :types="types.name">
                {{ types.name.toUpperCase() }}
              </base-type-tag>
            </div>
          </div>
          <div class="no-damage">
            <p class="dmg-title">No Damage To</p>
            <div
              class="pokemon-types"
              v-for="types in typeData.damage_relations.no_damage_to"
              :key="types"
            >
              <base-type-tag :types="types.name">
                {{ types.name.toUpperCase() }}
              </base-type-tag>
            </div>
          </div>
        </div>
      </div>

      <div class="basic-stats">
        <h3 class="details-title">Pokemon stat</h3>
        <table class="stat-table">
            <tr v-for="stats in pokemonDetails.responseData.stats" :key="stats">
                <td class="stat-name">{{ stats.stat.name.toUpperCase() }}</td>
                <td class="stat-value">{{ stats.base_stat }}</td>
            </tr>
        </table>
      </div>
    </div>
  </div>
  <div v-else>
    <base-spinner></base-spinner>
  </div>
</template>

<script>
export default {
  props: ["id"],
  data() {
    return {
      selectedPokemon: null,
      isLoading: true,
    };
  },
  computed: {
    pokeId() {
      return this.$route.params.id;
    },
    pokemonDetails() {
      console.log(this.$store.getters["pokemon/pokemonDetails"]);
      return this.$store.getters["pokemon/pokemonDetails"];
    },
    hasDetails() {
      return this.$store.getters["pokemon/hasDetails"];
    },
    pokemonGenderPercentage() {
      let femaleRatio = parseInt(
        this.pokemonDetails.pokemonSpecies.gender_rate
      );
      if (femaleRatio === -1) {
        return "Genderless";
      }
      let malePercentage = 100 - femaleRatio * 10;
      return `Male: ${malePercentage}%, Female: ${femaleRatio * 10}%`;
    },
  },
  created() {
    this.loadPokemonDetails(this.pokeId);
  },
  methods: {
    async loadPokemonDetails(id) {
      this.isLoading = true;
      try {
        await this.$store.dispatch("pokemon/loadPokemonDetails", id);
      } catch (err) {
        this.error = err.message || " Something went wrong";
        console.log(err);
      }
      this.isLoading = false;
    },
    nextPageLink() {
      this.loadPokemonDetails(parseInt(this.$route.params.id) + 1);
      return this.$router.push({
        name: "pokemonDetails",
        params: { id: parseInt(this.$route.params.id) + 1 },
      });
    },
    previousPageLink() {
      let pokemonId = parseInt(this.$route.params.id) - 1;
      if (pokemonId < 1) {
        alert("You have reached the minimum page!");
        return;
      }
      this.loadPokemonDetails(pokemonId > 0 ? pokemonId : 1);
      return this.$router.push({
        name: "pokemonDetails",
        params: { id: pokemonId },
      });
    },
    toolText(index) {
      return this.$store.getters["pokemon/pokemonDetails"].abilityText[index]
        .effect;
    },
  },
};
</script>

<style scoped>
.general-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.pokemon-info {
  /* background-color: rgba(255, 0, 0, 0.8);
   */
   background-color:rgb(167, 167, 167);
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  text-align: center;
}

.pokemon-types {
  display: inline-block;
  padding: 5px;
}

.pokemon-image {
  text-align: center;
  
}
.pokemon-image > img{
min-width: 15rem;
  width: 25rem;
  height:auto;
}

.biodata-title {
  color: white;
  font-weight: 700;
  font-size: 18px;
}

.biodata-value {
  font-weight: 700;
}

.pokemon-name {
  text-align: center;
}

.abilities {
  display: inline-block;
  padding: 5px;
}

.ability-button {
  padding: 10px;
  background-color: black;
  color: white;
  font-weight: 700;
  border-radius: 8px;
}
.damage-info {
  display: flex;
  flex-direction: row;
}

.half-damage,
.double-damage,
.no-damage {
  background-color: rgba(255, 0, 0, 0.8);
  margin: 3px;
  padding: 15px;
  min-width: 50%;
  border-radius: 5px;
  font-weight: 700;
  color: white;
}

.dmg-title {
  text-align: center;
}

.pokemon-type{
text-align:center;
}

.details-title{
    margin: 1.5rem 0;
    text-align:center;
}

.pokemon-name{
    margin: 2rem 5rem;
}

.stat-name,
.stat-value {
    text-align: center;
    font-size: 20px;
    border: 1px solid grey;
}

.stat-name {
    background-color: rgba(255, 0, 0, 0.8);
    border: 2px solid rgba(255, 0, 0, 0.1);
    color:white;
    font-weight:500;
}

.stat-value {
    padding: 2px 12px;
    border: 2px solid rgba(255, 0, 0, 0.8);
    background-color: snow;

}
.stat-table{
    width:90%;
    margin-bottom: 35px;
}

.navigation-button{
    display:inline-block;
}

.next{
    float:right;
}
.previous{
    float:left;
}

.details-header{
    width:100%;
    display:flex;
    justify-content: center;
    align-items: center;
}
</style>