<template>
  <div>
    <h2>Pokemon List</h2>
    <section class="container" v-if="!isLoading">
      <div v-if="!hasPokemons">
        <p>No pokemon has been found</p>
      </div>
      <div v-else>
        <div class="filter-section">
          <pokemon-filter @search-pokemon="setPokemon" @data-per-page="setDataPerPage"></pokemon-filter>
          <pokemon-sort @set-sequence="setSequence"></pokemon-sort>
        </div>

        <div class="card-deck">
          <pokemon-item v-for="pokemon in filteredPokemon" :key="pokemon.id" :id="pokemon.id" :imageUrls="pokemon"
            :stats="pokemon.stats" :name="pokemon.name"></pokemon-item>
        </div>

      </div>
      <base-button mode="load" v-if="hasPokemons" @click="loadMoreData()">Load More</base-button>
    </section>
    <div v-else>
      <base-spinner></base-spinner>
    </div>
  </div>
</template>


<script>
import PokemonItem from "../components/pokemon/PokemonItem.vue";
import PokemonFilter from "../components/pokemon/PokemonFilter.vue";
import PokemonSort from "../components/pokemon/PokemonSort.vue";

export default {
  data() {
    return {
      searchText: "",
      searchCriteria: "",
      pageOffset: 0,
      end: 15,
      data: 0,
      sequence: 'asc',
      stats: 'id',
      isLoading: true,
    };
  },
  components: {
    PokemonItem,
    PokemonFilter,
    PokemonSort
  },
  created() {
    this.loadPokemons();
  },
  computed: {
    filteredPokemon() {
      const pokemons = this.$store.getters["pokemon/pokemons"];

      if (this.searchText !== "" && this.searchText !== null && this.searchCriteria !== null) {
        switch (this.searchCriteria) {
          case "name":
            return pokemons.filter(
              (pokemon) => pokemon.name.trim().toLowerCase() === this.searchText.trim().toLowerCase()
            );
          case "id":
            return pokemons.filter((pokemon) => pokemon.id === parseInt(this.searchText));
        }
      }

      switch (this.stats) {
        case "id":
          if (this.sequence === 'asc') {
            return pokemons.sort(function (a, b) {
              return a.id - b.id
            }).slice(this.pageOffset, this.end);

          }
          return pokemons.reverse(function (a, b) {
            return a.id - b.id

          }).slice(this.pageOffset, this.end);

        case "hp":
          if (this.sequence === 'asc') {
            return pokemons.sort(function (a, b) {
              return a.stats[0].base_stat - b.stats[0].base_stat
            }).slice(this.pageOffset, this.end);
          }
          return pokemons.reverse(function (a, b) {
            return a.stats[0].base_stat - b.stats[0].base_stat
          }).slice(this.pageOffset, this.end);
        case "att":
          if (this.sequence === 'asc') {
            return pokemons.sort(function (a, b) {
              return a.stats[1].base_stat - b.stats[1].base_stat
            }).slice(this.pageOffset, this.end);
          }
          return pokemons.reverse(function (a, b) {
            return a.stats[1].base_stat - b.stats[1].base_stat
          }).slice(this.pageOffset, this.end);
        case "def":
          if (this.sequence === 'asc') {
            return pokemons.sort(function (a, b) {
              return a.stats[2].base_stat - b.stats[2].base_stat
            }).slice(this.pageOffset, this.end);
          }
          return pokemons.reverse(function (a, b) {
            return a.stats[2].base_stat - b.stats[2].base_stat
          }).slice(this.pageOffset, this.end);
        default:
          if (this.sequence === 'asc') {
            return pokemons.sort(function (a, b) {
              return a.id - b.id
            }).slice(this.pageOffset, this.end);

          }
          return pokemons.reverse(function (a, b) {
            return a.id - b.id

          }).slice(this.pageOffset, this.end);
      }

    },
    hasPokemons() {
      return this.$store.getters["pokemon/hasPokemons"];
    },
  },
  methods: {
    async loadPokemons() {
      this.isLoading = true;
      try {
        await this.$store.dispatch("pokemon/loadPokemons");


      } catch (err) {
        this.error = err.message || " Something went wrong";
        console.log(err);
      }
      this.isLoading = false;
    },
    setPokemon(searchCriteria) {
      this.searchText = searchCriteria.searchText;
      this.searchCriteria = searchCriteria.criteria;
    },
    setDataPerPage(dataPerPage) {
      this.data = dataPerPage;
      this.end = dataPerPage;
    },
    loadMoreData() {
      if (this.data === 0) {
        this.end = parseInt(this.end) + 15

      } else {
        this.end = parseInt(this.end) + parseInt(this.data)

      }
    },
    setSequence(sequence) {
      this.sequence = sequence.sequence;
      this.stats = sequence.stats;
    }
  },
};
</script>

<style scoped>
.card-deck {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.filter-section {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

h2 {
  text-align: center;
}
</style>
