<template>
  <div v-if="!isLoading">
    <h2>Favourite Pokemon List</h2>
    <section class="container">
      <div v-if="!isFavourite">
        <p>No favourite pokemon added</p>
      </div>
      <div v-else>
        <pokemon-filter @search-pokemon="setPokemon" @data-per-page="setDataPerPage"></pokemon-filter>
  <div class="card-deck">
    <pokemon-item
          v-for="pokemon of filteredPokemon"
          :key="pokemon.id"
          :id="pokemon.id"
          :imageUrls="pokemon"
          :stats="pokemon.stats"
          :name="pokemon.name"
        ></pokemon-item>
  </div>
        
      </div>
    </section>
  </div>
  <div v-else>
    <base-spinner></base-spinner>
  </div>
</template>

<script>
import PokemonItem from "../components/pokemon/PokemonItem.vue";
import PokemonFilter from "../components/pokemon/PokemonFilter.vue";

export default {
  components: {
    PokemonItem,
    PokemonFilter
  },
  data(){
    return{
        isFavourite: false,
        searchText: "",
      searchCriteria: "",
      isLoading: true,
      pageOffset: 0,
      end: 15,
    }
  },
  created() {
    this.loadFavouritePokemon();
  },
  computed: {
    filteredPokemon() {
      const pokemons = this.$store.getters["pokemon/favouritePokemon"];

        if (this.searchText !== "" && this.searchText !== null && this.searchCriteria !== null) {
        switch (this.searchCriteria) {
          case "name":
            return pokemons.filter(
              (pokemon) => pokemon.name.trim().toLowerCase() === this.searchText.trim().toLowerCase().slice(this.pageOffset, this.end)
            );
          case "id":
            return pokemons.filter((pokemon) => pokemon.id === parseInt(this.searchText)).slice(this.pageOffset, this.end);
        }
      }
      return pokemons.slice(this.pageOffset, this.end)
    },
  },
  methods: {
    async loadFavouritePokemon() {
      this.isLoading= true;
      const favPokemon = JSON.parse(localStorage.getItem("favouritePokemon"));

      try {
        if (favPokemon !== null) {
          await this.$store.dispatch("pokemon/loadFavouritePokemon", favPokemon);
          this.isFavourite = true;
        }
      } catch (err) {
        this.error = err.message || " Something went wrong";
        console.log(err);
      }
      this.isLoading=false;
    },
    setPokemon(searchCriteria) {
      this.searchText = searchCriteria.searchText;
      this.searchCriteria = searchCriteria.criteria;
    },
    setDataPerPage(dataPerPage) {
      this.end = dataPerPage;
    },
  },
};
</script>

<style scoped>
.card-deck {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

h2 {
  text-align: center;
}
</style>
