import axios from 'axios';

export default {
    namespaced: true,
    mutations: {
        setPokemons(state, payload) {
            state.pokemons = payload;
        },
        setPokemonDetails(state, payload) {
            state.pokemonDetails = payload;
        },
        setFavouritePokemon(state, payload){
            state.favouritePokemon = payload;
        }
    },
    actions: {
        async loadPokemons(context) {
            let responseData = '';
            const pokemonData = [];


            responseData = await axios.get(`https://pokeapi.co/api/v2/pokemon?limit=1000&offset=0`)
                .then(item => {
                    return item.data;
                })

            for (const data of responseData.results) {
                const secondaryData = await axios.get(data.url);
                const pokemon = {
                    id: secondaryData.data.id,
                    imageUrls: secondaryData.data.sprites,
                    name: secondaryData.data.name,
                    stats: secondaryData.data.stats
                }
                pokemonData.push(pokemon)
            }
            context.commit('setPokemons', pokemonData)
        },


        async loadPokemonDetails(context, id) {
            const responseData = await axios.get(`https://pokeapi.co/api/v2/pokemon/${id}`)
                .then(item => {
                    return item.data;
                })
            const pokemonSpecies = await axios.get(`https://pokeapi.co/api/v2/pokemon-species/${id}/`)
                .then(gender => {
                    return gender.data
                })

            const typeData = []
            for (let i = 0; i < responseData.types.length; i++) {
                const pokemonType = await axios.get(responseData.types[i].type.url);
                typeData.unshift(pokemonType.data)

            }
            const abilityData = []
            for (let url of responseData.abilities) {
                const pokemonType = await axios.get(url.ability.url);
                abilityData.unshift(pokemonType.data);
            }

            let abilityText = []
            for(let abilityItem of responseData.abilities){
                const pokemonAbility = await axios.get(abilityItem.ability.url);
                for(let abilityDetails of pokemonAbility.data.effect_entries){
                    abilityText.unshift(abilityDetails);
                   
                }
                
            }
            abilityText = abilityText.filter(item => {
                return item.language.name === "en"
            });

            
            context.commit('setPokemonDetails', { responseData, pokemonSpecies, typeData, abilityData, abilityText })

        },
        async loadFavouritePokemon(context, payload) {
            const favouriteData = [];
            let responseData = '';
            for(let id of payload){
                responseData = await axios.get(`https://pokeapi.co/api/v2/pokemon/${id.pokeId}`)
                const pokemon = {
                    id: responseData.data.id,
                    imageUrls: responseData.data.sprites,
                    name: responseData.data.name,
                    stats: responseData.data.stats
                }
                favouriteData.push(pokemon)
            }
            console.log(favouriteData)

            context.commit('setFavouritePokemon', favouriteData)

        }
    },
    getters: {
        pokemons(state) {
            return state.pokemons;
        },
        pokemonDetails(state) {
            return state.pokemonDetails;
        },
        favouritePokemon(state){
            return state.favouritePokemon;
        },
        hasPokemons(state) {
            return state.pokemons && state.pokemons.length > 0;
        },
        hasDetails(state) {
            return state.pokemonDetails;
        },


    }
}