import { createRouter, createWebHistory } from 'vue-router';

import PokemonList from './pages/PokemonList.vue';
import PokemonDetails from './pages/PokemonDetails.vue';
import FavouritePokemonList from './pages/FavouritePokemonList.vue';
import NotFound from './pages/error/NotFound.vue';

const router = createRouter({
history: createWebHistory(),
routes: [

    { path: '/', redirect: '/pokemons'},
    { path: '/pokemons',component: PokemonList},
    { path: '/pokemon/:id', name: 'pokemonDetails',component: PokemonDetails },
    { path: '/favourite', component: FavouritePokemonList },
    { path: '/:notFound(.*)', component: NotFound }
]
});

export default router;